:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greyBg: hsl(0, 0%, 96%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
}

.header {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: transparent;
  padding: 1rem;
  width: 100%;
  z-index: 1000;
}
.logoDiv .logo {
  color: var(--whiteColor);
  font-weight: 500;
  cursor: pointer;
  align-items: center;
  justify-content: center;
}
.logoDiv .logo .icon {
  color: var(--PrimaryColor);
  font-size: 25px;
  margin-right: 10px;
}
@media screen and (max-width: 768px) {
  .navBar {
    background-color: var(--greyBg);
    position: absolute;
    left: -500%;
    top: 0;
    height: max-content;
    width: 100%;
    padding: 1rem;
    box-shadow: 0 2px 4px rgba(209, 209, 209, 0.904);
    transition: 0.5s ease;
    z-index: 1000;
  }
  .navLists {
    flex-direction: column;
    width: 100%;
    margin: auto;
    justify-content: center;
  }
  .navItem {
    padding: 1rem 0;
  }
  .navLink {
    color: var(--textColor);
    font-size: 0.9rem;
    font-weight: 600;
  }
  .navLink:hover {
    color: var(--PrimaryColor);
  }
  .headerBtns {
    flex-direction: column;
  }
  .btn {
    margin-top: 1rem;
  }
  .btn a {
    font-weight: 600;
  }
  .btn:hover a {
    color: var(--whiteColor);
  }

  .loginBtn {
    background: none;
    border: 1px solid var(--PrimaryColor);
  }
  .loginBtn a {
    color: var(--PrimaryColor);
  }
  .loginBtn:hover {
    background-color: var(--HoverColor);
    border-color: var(--HoverColor);
  }
  .loginBtn a:hover {
    color: var(--whiteColor);
  }
  .closeNavbar {
    position: absolute;
    top: 1rem;
    right: 1.5rem;
  }
  .closeNavbar:hover .icon {
    color: var(--HoverColor);
  }
  .activeNavbar{
    left: 0 ;
  }
  .togggleNavbar .icon{
    font-size: 25px;
    color: var(--whiteColor);
  }
  .togggleNavbar:hover .icon{ 
    color: var(--PrimaryColor);
  }
}
.activeHeader{
  background-color: var(--greyText);
  box-shadow: var(--boxShadow);
}
@media (min-width: 769px) {
  .closeNavbar, .togggleNavbar {
  display: none;
  }
  .header{
    padding: 1.5rem 2rem ;
  }
  .navLists{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
  .navLists .btn{
    margin-left: 1rem;
  }
  .navLists .btn a{
    color: var(--whiteColor);
    font-weight: 500;
  }
  .navLink{
    color: var(--whiteColor);
    padding: 0 0.45rem;
    font-size: 0.9rem;
    font-weight: 400;
  }
  .navLink:hover{
    color: var(--PrimaryColor);
  }
  .loginBtn{
    background: none;
    border: 1px solid var(--PrimaryColor);
  }
  .loginBtn:hover{
    background-color: var(--PrimaryColor);
  }
  .loginBtn a{
    color: var(--whiteColor);
  }
}
@media (min-width: 960px) {
  .header .navItem{
    padding: 0 0.7rem ;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 1rem 8.5rem ;
  }
}