/* Color */
:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greyBg: hsl(0, 0%, 96%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
  --boxShadow: 0 2px 8px 2px rgb(178, 178, 178, 0.334);
}
.blog .mainContainer {
  gap: 1rem;
}
.blog .mainContainer .singlePost {
  gap: 2rem;
}
.blog .mainContainer .singlePost .imgDiv {
  height: 200px;
  width: 100%;
  border-radius: 1rem;
  border: 5px solid var(--greyBg);
  box-shadow: 0 2px 8px 2px rgb(178, 178, 178, 0.406);
  overflow: hidden;
}
.blog .mainContainer .singlePost .imgDiv:hover img {
  transform: scale(1.1);
  transition: 3s ease;
}
.blog .mainContainer .singlePost .imgDiv img {
  height: 100%;
  object-fit: cover;
}
.blog .mainContainer .postDetails h3 {
  color: var(--blackColor);
  font-weight: 600;
  padding-bottom: 10px;
}
.postDetails p {
  color: var(--textColor);
  font-weight: 500;
  font-size: 13px;
}
.singlePost a {
  color: var(--PrimaryColor);
  font-weight: 600;
  font-size: 15px;
  width: max-content;
  padding-top: 0.5rem;
}
.singlePost a:hover {
  color: var(--HoverColor);
  font-weight: 600;
}
.singlePost .icon {
  transform: translateX(10px);
}
@media (min-width: 884px) {
  .blog .mainContainer {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 884px) {
  .blog .mainContainer .singlePost {
    grid-template-columns: repeat(2, 1fr);
  }
}
