/* Color */
:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greyBg: hsl(0, 0%, 96%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
  --boxShadow: 0 2px 8px 2px rgb(178, 178, 178, 0.334);
}
.mainContent {
  gap: 1rem;
}
.singleOffer:hover {
  transform: translateY(-7px);
}
.mainContent .singleOffer {
  border-radius: 1rem;
  overflow: hidden;
  padding: 0.5rem;
  box-shadow: 0 2px 8px 2px rgb(178, 178, 178, 0.189);
}
.mainContent .singleOffer .icon {
  font-size: 20px;
  color: var(--textColor);
}
.mainContent .singleOffer small {
  font-weight: 500;
  color: var(--textColor);
}
.mainContent .singleOffer .destImg {
  position: relative;
  height: 200px;
  width: 100%;
  overflow: hidden;
  border-radius: 1rem;
}
.mainContent .singleOffer .destImg img {
  height: 100%;
}
.discount {
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 3rem;
  padding: 5px 10px;
  background-color: var(--PrimaryColor);
  color: var(--whiteColor);
  font-size: 10px;
  font-weight: 500;
}
.offerBody {
  padding: 1rem;
}
.offerBody .price {
  justify-content: space-between;
}
.offerBody .price h4 {
  font-weight: 700;
  font-size: 1.1rem;
  color: var(--blackColor);
}
.offerBody .price .status {
  padding: 5px 10px;
  background-color: var(--greyBg);
  color: var(--HoverColor);
  border-radius: 3rem;
  font-weight: 600;
  font-size: 10px;
}
.amenities {
  flex-wrap: wrap;
  gap: 0.5rem;
  padding: 1rem 0;
  justify-content: flex-start;
  width: 100%;
}
.singeAmenity {
  background-color: var(--greyBg);
  gap: 0.5rem;
  padding: 5px 10px;
  border-radius: 5px;
}
.offerBody .btn {
  color: var(--whiteColor);
  text-align: center;
  width: 100%;
  justify-content: center;
  padding: 0.6rem;
  gap: 0.5rem;
  font-weight: 600;
}
.offerBody .btn .icon {
  color: var(--whiteColor);
}
@media (min-width: 480px) {
  .offer .mainContent {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 844px) {
  .offer .mainContent {
    grid-template-columns: repeat(3, 1fr);
  }
}
