:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greyBg: hsl(0, 0%, 96%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
}
.home {
  height: 80vh;
  background: linear-gradient(rgb(33, 33, 33, 0.522), rgb(33, 33, 33, 0.522)),
    url("../../assets/homerent.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  position: relative;
}
.homeText {
  text-align: center;
}
.title {
  color: var(--whiteColor);
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.5rem;
}
.subTitle {
  color: var(--whiteColor);
  font-weight: 300;
  opacity: 0.9;
  font-size: 13px;
  padding: 1rem 0;
  max-width: 70%;
  text-align: center;
  margin: auto;
}
.homeText .btn {
  padding: 0.6rem 1.5rem;
}
.homeCard {
  width: 80%;
  border-radius: 1rem;
  padding: 1rem;
  background: var(--whiteColor);
  gap: 1rem;
  position: absolute;
  bottom: -35%;
  /* justify-content: center; */
  transform: translateX(-50%);
  left: 50%;
  box-shadow: 0 2px 8px 4px rgba(178, 178, 178, 0.45);
}
.locationDiv,
.distDiv,
.priceDiv {
  width: 100%;
}
label {
  display: block;
  color: var(--textColor);
  font-size: 13px;
  font-weight: 300;
  margin-bottom: 0.4rem;
}
input {
  width: 100%;
  background-color: var(--inputColor);
  border-radius: 10px;
  border: none;
  padding: 10px;
  font-size: 13px;
  font-weight: 500;
}
input::placeholder {
  opacity: 0.5;
  font-weight: 400;
  font-size: 13px;
}
input:focus {
  outline: none;
}
.homeCard .btn {
  color: var(--whiteColor);
  width: 100%;
  padding: 0.6rem 1.5rem;
  font-weight: 600;
}
@media (min-width: 480px) {
  .home .secContainer .homeCard {
    bottom: -18px;
    width: 90%;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 556px) {
  .home .secContainer .homeText .title {
    font-size: 2.5rem;
  }
  .home .secContainer .homeText .subTitle {
    font-size: 18px;
  }
}
@media (min-width: 720px) {
  .home .secContainer .homeCard {
    bottom: -10%;
    grid-template-columns: repeat(4, 1fr);
  }
  .home .secContainer .btn{
    transform: translateY(10px) !important;
  }
  .home .secContainer .homeText {
    padding: 1rem 10rem;
  }
  .home .secContainer .homeText .title {
    font-size: 3.5rem;
    margin: auto;
    line-height: 4rem;
  }
  .home .secContainer .homeText .subTitle {
    width: 70%;
  }
}

@media (min-width: 1024px) {
  .home .secContainer .homeText .subTitle {
    font-size: 20px;
  }
  .home .secContainer .homeCard{
    width: 70%;
  }
}
