/* Color */
:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greyBg: hsl(0, 0%, 96%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
  --boxShadow: 0 2px 8px 2px rgb(178, 178, 178, 0.334);
}
.about .title {
  font-size: 1.5rem;
  padding-bottom: 2rem;
  line-height: 1.5rem;
  color: var(--blackColor);
  text-align: center;
}
.mainContent {
  gap: 1.5rem;
}
.about .mainContent .singleItem {
  text-align: center;
}
.about .singleItem img {
  background-color: rgba(247, 144, 54, 0.265);
  width: 100px;
  padding: 1rem;
  border-radius: 50%;
}
.about .singleItem h3 {
  color: var(--blackColor);
  padding: 0.5rem;
  font-weight: 700;
}
.about .singleItem p {
  color: var(--textColor);
  padding: 0.5rem;
  font-size: 13px;
  font-weight: 500;
}
.videoCard {
  margin-top: 2rem;
  background: linear-gradient(rgba(0, 0, 0, 0.73), rgba(0, 0, 0, 0.609)),
    url("../../assets/map.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.videoCard .cardContent {
  gap: 2rem;
  padding: 2rem 1rem;
}
.cardText h2 {
  color: var(--whiteColor);
  font-weight: 600;
  padding-bottom: 1rem;
}
.cardText p {
  color: var(--whiteColor);
  font-weight: 400;
  opacity: 0.7;
  font-size: 12px;
  letter-spacing: 1px;
}
.cardVideo {
  border: 2px solid var(--whiteColor);
  height: 200px;
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
}
.cardVideo video {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
@media (min-width: 720px) {
  .about .videoCard .cardContent{
    gap: 4rem;
    padding: 4rem 3rem;
  }
  .about .videoCard .cardContent .cardText h2{
    font-size: 2rem;
  }
  .about .videoCard .cardContent .cardText p{
    font-size: 15px;
    opacity: 0.7;
  }
  .about .cardContent{
    grid-template-columns: repeat(2,1fr) !important;
  }

}
@media (min-width: 720px) {
  .about .mainContent{
    padding: 2.5rem 0;
  }
}
@media (min-width: 556px) {
  .about .mainContent{
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 845px) {
  .about .mainContent{
    grid-template-columns: repeat(3, 1fr);
  }
}
