:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greyBg: hsl(0, 0%, 96%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
  --boxShadow: 0 2px 8px 2px rgb(178, 178, 178, 0.334);
}
.popular {
  padding-top: 15rem;
}
.secHeader {
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
}
.secHeader .textDiv .secTitle {
  padding-bottom: 0.5rem;
  color: var(--blackColor);
  line-height: 1.5rem;
}
.textDiv p {
  font-size: 13px;
  color: var(--textColor);
  font-weight: 500;
}
.iconsDiv {
  gap: 0.5rem;
}
.iconsDiv .icon {
  color: var(--blackColor);
  background-color: var(--greyText);
  padding: 0.5rem;
  font-size: 2.5rem;
  border-radius: 50%;
}
.iconsDiv .icon:hover {
  background-color: var(--blackColor);
  color: var(--whiteColor);
}
.iconsDiv .leftIcon {
  background-color: var(--blackColor);
  color: var(--whiteColor);
}
.iconsDiv .leftIcon:hover {
  background-color: var(--greyText);
  color: var(--blackColor);
}
.mainContainer {
  padding: 2rem 0;
  gap: 1rem;
}
.singleDestination {
  overflow: hidden;
  border-radius: 1rem;
  box-shadow: var(--boxShadow);
}
.destImg {
  height: 200px;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.destImg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.overlayInfo {
  position: absolute;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 0;
  flex-direction: column;
  background: linear-gradient(rgba(0, 0, 0, 0.846), rgba(0, 0, 0, 0.759));
  bottom: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
}
.singleDestination:hover .overlayInfo{
  overflow: visible;
  padding: 1rem 1.5rem;
  height: 100%;

}
.overlayInfo h3 {
  color: var(--whiteColor);
  font-weight: 600;
}
.overlayInfo p {
  color: var(--whiteColor);
  font-size: 13px;
  opacity: 0.7;
}
.overlayInfo .icon {
  position: absolute;
  right: 10px;
  bottom: 10px;
  padding: 0.5rem;
  font-size: 2.5rem;
  background-color: var(--PrimaryColor);
  border-radius: 50%;
  color: var(--whiteColor);
  border: 1px solid transparent;
  transition: 0.2s ease;
}
.overlayInfo .icon:hover {
  border-color: var(--PrimaryColor);
  background-color: var(--blackColor);
  color: var(--PrimaryColor);
}

.destFooter{
  padding: 1rem;
}
.number{
  font-size: 2rem;
  font-weight: 700;
  position: relative;
  color: var(--blackColor);
  position: relative;
  width: max-content;
}
.number::after{
  content: "";
  position: absolute;
  background-color: var(--PrimaryColor);
  width:45px;
  height: 5px;
  top: 45%;
  right: -150%;
  border-radius: 1rem;
}
.destText{
  justify-content: space-between;
}
.destText h6{
  color: var(--blackColor);
  font-size: 15px;
}
.destText .flex{
  font-size: 16px;
  font-weight: 600;
  align-items: flex-start;
  transform: translateY(5px);
}
@media screen and (min-width: 480px) {
  .popular{
    padding-top: 10rem;
  }
}
@media screen and (min-width: 556px) {
  .popular .secContainer .secHeader{
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .popular .secContainer .secHeader .textDiv{
    text-align: start;
    max-width: 70%;
  }
  .popular .secContainer .secHeader .iconsDiv{
   justify-content: flex-end;
  }
}
@media screen and (min-width: 720px) {
  .popular {
    padding-top: 7rem;
  }
}
@media screen and (min-width: 1024px) {
  .popular {
    padding-top: 10rem;
  }
  .popular .textDiv .secTitle {
    font-size: 2.2rem;
  }
  .popular .textDiv  p{
    font-size: 15px;
  }
  .popular .mainContainer{
    grid-template-columns: repeat(4,1fr);
    gap: 1.5rem;
  }
}